            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="ĞĞ¾Ğ¼ĞµÑ ÑĞµĞ»ĞµÑĞ¾Ğ½Ğ° Ğ´Ğ¾Ğ»Ğ¶ĞµĞ½ ÑĞ¾ÑÑĞ¾ÑÑÑ ÑĞ¸ÑÑ Ğ¸ Ğ¼Ğ¾Ğ¶ĞµÑ ÑĞ¾Ğ´ĞµÑĞ¶Ğ°ÑÑ Ğ¿ÑĞ¾Ğ±ĞµĞ»Ñ, ÑĞ¸ÑĞµ, ĞºÑÑĞ³Ğ»ÑĞµ ÑĞºĞ¾Ğ±ĞºĞ¸ Ğ¸ Ğ¼Ğ¾Ğ¶ĞµÑ Ğ½Ğ°ÑĞ¸Ğ½Ğ°ÑÑÑÑ Ñ +"
